<template lang="pug">
.container
  h1 Latest Posts
  .create-post
    label(for="create-post") Say Something...
    input#create-post(type="text" v-model="text" placeholder="Create a post")
    button(@click="createPost") Post!
  hr
  p.error(v-if="error") {{ error.message }}
  .posts-container
    .post(v-for="post in posts" :key="post.uuid" @dblclick="deletePost(post.uuid)")
      span {{ formatDate(post.createdAt) }}
      p.text {{ post.text }}
</template>

<script>
import PostService from "../services/posts"

export default {
  name: "Post",
  data() {
    return {
      posts: [],
      error: "",
      text: "",
    }
  },
  methods: {
    formatDate(timestamp) {
      const date = new Date(timestamp)
      return `${date.getDate()}/${date.getMonth()}/${date.getFullYear()}`
    },
    async createPost() {
      try {
        await PostService.createPost(this.text)
        this.posts = await PostService.getPosts()
      } catch (err) {
        this.error = err
      }
    },
    async deletePost(uuid) {
      try {
        await PostService.deletePost(uuid)
        this.posts = await PostService.getPosts()
      } catch (err) {
        this.error = err
      }
    },
  },
  async created() {
    try {
      this.posts = await PostService.getPosts()
    } catch (err) {
      this.error = err
    }
  },
}
</script>

<style lang="scss" scoped>
.container {
  max-width: 800px;
  margin: 0 auto;
}

p.error {
  border: 1px solid #ff5b5f;
  background-color: #ffc5c1;
  padding: 10px;
  margin-bottom: 15px;
}

.post {
  position: relative;
  border: 1px solid #5bd658;
  background-color: #bcffb8;
  padding: 10px 10px 30px 10px;
  margin-bottom: 15px;
}

.created-at {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px 15px 5px 15px;
  background-color: darkgreen;
  color: white;
  font-size: 13px;
}

p.text {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 0;
}
</style>
