import axios from "axios"

const url = "/api/posts"

export default class PostService {
  // Get Posts
  static getPosts() {
    return axios.get(url).then(({ data }) => data.posts)
  }

  // Create Post
  static createPost(text) {
    return axios.post(url, { text })
  }

  // Delete Post
  static deletePost(uuid) {
    return axios.delete(url + "/" + uuid)
  }
}
